<template>
  <div>
    <p>Index Of Format</p>
    <p>
      <router-link to="/">back to the contents</router-link>
    </p>
    <br>
    <table border="1">
      <tr>
        <th>index of str</th>
        <th>index start plus</th>
        <th>index end subtrac</th>
      </tr>
      <tr>
        <td><input v-model="index_of_str"></td>
        <td><input v-model="index_start_plus"></td>
        <td><input v-model="index_end_subtrac"></td>
      </tr>
    </table>
    <textarea v-model="source" id="source" rows="50" cols="80" placeholder="source text"></textarea>
    <textarea v-model="output" id="output" rows="50" cols="80" placeholder="output" readonly></textarea>
    <br>
    <div>
      <input value="handle" type="button" @click="handle()">
    </div>
  </div>
</template>

<script>

export default {
  name: 'IndexOfFormatView',
  data () {
    return {
      index_of_str: ' as ',
      index_start_plus: 4,
      index_end_subtrac: 1,
      source: 'name as name,\nage as age,',
      output: ''
    }
  },
  methods: {
    handle () {
      this.output = ''
      const plus = Number.parseInt(this.index_start_plus)
      const subtrac = Number.parseInt(this.index_end_subtrac)
      let fieldTemp = ''
      if (this.index_of_str === '') {
        return
      }
      const sourceArr = this.source.split('\n').map(e => e.trim()).filter(e => e)
      for (let i = 0; i < sourceArr.length; i++) {
        const ii = sourceArr[i]
        const index = ii.indexOf(this.index_of_str)
        if (i === sourceArr.length - 1) {
          fieldTemp = fieldTemp + ii.substring(index + plus, ii.length - subtrac)
        } else {
          fieldTemp = fieldTemp + ii.substring(index + plus, ii.length - subtrac) + '\n'
        }
      }
      this.output = fieldTemp
      navigator.clipboard.writeText(this.output).then(() => console.log('copied'))
    }
  }
}
</script>
